  /*공통  */
body,div,ul,li,a,ol,h1,span,header,span,nav,p,iframe,footer{margin: 0;padding: 0;}

body{position: relative;width: 100%;}
li{list-style: none;}
a{text-decoration: none;}
.clearfix::after{
    content:"";
    clear: both;
    display: block;
}
.float--left{
    float: left;
}
.float--right{
    float: right;
}

div.wrap{width: 100%;}
div.inner{width: 345px;margin: 0 auto;margin-top: 10px;}
header{margin-bottom: 20px;height: 40px;}
h1 a{display:block;font-size: 17px;line-height:40px;
  color: rgb(1, 1, 109);font-weight: bold;
  border: 1px solid #000;padding: 0 10px;border-radius: 6px;
background-color: rgb(244, 250, 255);
box-shadow:0 0 2px rgba(0,0,0,.75);
}
header p{padding: 0 5px;border-radius: 4px;
  box-shadow:inset 0 1px 5px rgba(0,0,0,0.075)
}
header span{display: block;font-size: 9px;height: 20px;line-height: 20px;text-align: right;font-weight: bold;}

.ul--group{}
ul.mainul{display: flex;flex-wrap:wrap;margin-bottom: 10px;}
ul.mainul li a{display: block;width: 113px;box-sizing: border-box;border: 1px solid #000;
  height: 35px;line-height: 35px;text-align: center;font-size: 16px;font-weight: bold;background-color: rgb(1, 1, 109);
margin: 1px;border-radius: 6px;box-shadow:0 0 2px rgba(0,0,0,.55);
}
ul.mainul li:nth-child(1) a{color: rgb(140, 255, 140);}
ul.mainul li:nth-child(1) a{color: #fff;background: #fff;border: none;box-shadow: none;}
/* ul.mainul li:nth-child(1) a{position: relative;top: 35px;background-color:rgb(0, 63, 0);color:#fff;font-size: 18px;} */
ul.mainul li:nth-child(2) a{color: rgb(255, 255, 255);}
ul.mainul li:nth-child(3) a{color: rgb(245, 119, 252);}
ul.mainul li:nth-child(3) a{color: #fff;background: #fff;border: none;box-shadow: none;}
ul.mainul li:nth-child(4) a{color: rgb(255, 174, 0);}
ul.mainul li:nth-child(4) a{color: #fff;background: #fff;border: none;box-shadow: none;}

nav{}
ol{display: flex;flex-wrap:wrap ;}
ol li a{display: block;width: 113px;box-sizing: border-box;border: 1px solid rgb(1, 1, 109);height: 70px;line-height: 70px;text-align: center;background: grey;
color: #fff;font-weight: bold;font-size: 18px;text-decoration: none;margin: 1px;
font-weight:bolder;border-radius: 6px;
box-shadow:inset 0 1px 5px rgba(0,0,0,0.075), 
                0 0 3px rgba(81,167,232,0.5)
}

/* 1번색 */
ol.m--mode1 li a{border-color: rgb(0, 63, 0);}
ol.m--mode1 li:nth-child(1) a,ol.m--mode1 li:nth-child(3) a,
ol.m--mode1 li:nth-child(5) a,ol.m--mode1 li:nth-child(7) a,
ol.m--mode1 li:nth-child(9) a,ol.m--mode1 li:nth-child(11) a,
ol.m--mode1 li:nth-child(13) a,ol.m--mode1 li:nth-child(15) a{color: rgb(0, 63, 0);background: #fff}
