ol.m--mode2 li:nth-child(6) a,ol.m--mode2 li:nth-child(8) a,
ol.m--mode2 li:nth-child(10) a,ol.m--mode2 li:nth-child(12) a,
ol.m--mode2 li:nth-child(14) a{color: #fff;background: rgb(1, 1, 109)}

/*  */
