ol.m--mode2 li:nth-child(6) a,ol.m--mode2 li:nth-child(8) a,
ol.m--mode2 li:nth-child(10) a{background: gray;}
/*  */

/* 3번색 */
ol.m--mode3 li a{border-color: rgb(91, 0, 95);}
ol.m--mode3 li:nth-child(1) a,ol.m--mode3 li:nth-child(3) a,
ol.m--mode3 li:nth-child(5) a,ol.m--mode3 li:nth-child(7) a,
ol.m--mode3 li:nth-child(9) a,ol.m--mode3 li:nth-child(11) a,
ol.m--mode3 li:nth-child(13) a,ol.m--mode3 li:nth-child(15) a{color: rgb(91, 0, 95);background: #fff}
