ol.m--mode3 li:nth-child(6) a,ol.m--mode3 li:nth-child(8) a,
ol.m--mode3 li:nth-child(10) a,ol.m--mode3 li:nth-child(12) a,
ol.m--mode3 li:nth-child(14) a{color: #fff;background: rgb(91, 0, 95)}
/* 4번색 */
ol.m--mode4 li a{border-color: rgb(114, 81, 10);}
ol.m--mode4 li:nth-child(1) a,ol.m--mode4 li:nth-child(3) a,
ol.m--mode4 li:nth-child(5) a,ol.m--mode4 li:nth-child(7) a,
ol.m--mode4 li:nth-child(9) a,ol.m--mode4 li:nth-child(11) a,
ol.m--mode4 li:nth-child(13) a,ol.m--mode4 li:nth-child(15) a{color: rgb(114, 81, 10);background: #fff}
ol.m--mode4 li:nth-child(2) a,ol.m--mode4 li:nth-child(4) a,
ol.m--mode4 li:nth-child(6) a,ol.m--mode4 li:nth-child(8) a,
ol.m--mode4 li:nth-child(10) a,ol.m--mode4 li:nth-child(12) a,
ol.m--mode4 li:nth-child(14) a{color: #fff;background: rgb(114, 81, 10)}


ol.m--mode1 li:nth-child(3) a,ol.m--mode1 li:nth-child(7) a{font-size: 14px;}
ol.m--mode1 li:nth-child(13) a{font-size: 11px;}

.if--group{position: absolute;top: -5px;}

