.diro--group{position: absolute;top: 0;width: 100%;background:#fff;}
.diro--group a{display: block;width: 130px;background:rgb(28, 28, 104);
  height: 30px;line-height: 30px;text-align: center;
  margin:10px;border: 1px solid #000;border-radius: 6px;font-weight: bold;color: #fff;
  box-shadow:0 0 2px rgba(0,0,0,.75);
}

.yogi{width: 345px;height: 370px;border: 1px solid #aaa;border-radius: 5px;
  box-shadow:0 0 5px rgba(0,0,0,.25);
}
.yogi iframe{width: 345px;height:370px;border-style: none;overflow: hidden;}



footer{margin-top: 20px;}
ul.fu--ul{display: flex;flex-wrap:wrap;margin-bottom: 20px;}
ul.fu--ul li a{display: block;width: 113px;box-sizing: border-box;border: 2px solid rgb(1, 1, 109);
  height: 35px;line-height: 35px;text-align: center;font-size: 15px;font-weight: bolder;
margin: 1px;color:#fff;border-radius: 4px;background-color:rgb(1, 1, 109);
box-shadow:0 0 5px rgba(1, 1, 109,.75);
}
ul.fu--ul li:nth-child(1) a{background:#fff;border: none;box-shadow:none;}
ul.fu--ul li:nth-child(2) a{background:#fff;border: none;box-shadow:none;}
ul.fu--ul li:nth-child(3) a{background:#fff;border: none;box-shadow:none;}
footer p span{color: red;font-size: 10px;display: block;text-align: center;}